import React, { useRef, useEffect } from 'react';

import Webcam from 'react-webcam';
import Typography from '@material-ui/core/Typography';
import CancelIcon from '@material-ui/icons/Cancel';
import Slide from '@material-ui/core/Slide';

import Image from '../types/Image';

const usePrevious = <T extends {}>(value: T): T | undefined => {
  const ref = useRef<T>();
  useEffect(() => {
    ref.current = value;
  });
  return ref.current;
};

interface Props {
  shotting: boolean
  setShotting: (shotting: boolean) => void
  image: Image
  windowWidth: number
}

function Kaodashi(props: Props) {
  const {
    shotting,
    setShotting,
    image,
    windowWidth,
  } = props;

  const kaodashiRef = useRef<HTMLDivElement>(null);
  const webcamRef = useRef<Webcam & HTMLVideoElement>(null);
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const imageRef = useRef<HTMLImageElement>(null);

  const headPosition = image.heads_position[0];
  const canvasWidth = windowWidth;
  const canvasHeight = image.height / image.width * canvasWidth;
  const cameraSize = image.head_width * 2;
  const cameraSizeResized = cameraSize / image.width * canvasWidth;
  const cameraPosition = {
    top: (headPosition[1] - cameraSize / 2) / image.width * canvasWidth,
    left: (headPosition[0] - cameraSize / 2) / image.width * canvasWidth,
  }
  const prevShotting = usePrevious(shotting);
  useEffect(() => {
    if (!shotting || shotting === prevShotting) {
      return;
    }
    if (
      !kaodashiRef.current
      || !webcamRef.current || !webcamRef.current.video
      || !canvasRef.current || !imageRef.current
    ) {
      return;
    }
    const ctx = canvasRef.current.getContext('2d');
    if (!ctx) {
      return;
    }
    ctx.fillStyle = 'white';
    ctx.fillRect(0, 0, canvasWidth, canvasHeight);
    ctx.save();
    ctx.translate(canvasWidth, 0);
    ctx.scale(-1, 1);
    ctx.drawImage(webcamRef.current.video, canvasWidth - cameraPosition.left - cameraSizeResized, cameraPosition.top);
    ctx.restore();
    ctx.drawImage(imageRef.current, 0, 0, canvasWidth, canvasHeight);
  }, [shotting, prevShotting, cameraPosition, canvasWidth, canvasHeight, cameraSizeResized]);

  return (
    <div ref={kaodashiRef} style={{ height: canvasHeight }}>
      <Webcam
        mirrored
        audio={false}
        videoConstraints={{ width: cameraSizeResized, height: cameraSizeResized }}
        ref={webcamRef}
        style={{ position: 'absolute', ...cameraPosition }}
      />
      <img
        src={`${process.env.PUBLIC_URL}/images/${image.key}.png`}
        alt={image.name}
        ref={imageRef}
        style={{ position: 'absolute', width: '100%' }}
      />
      <Slide in={shotting} direction='up'>
        <div style={{
          position: 'absolute',
          width: '100%',
          height: '100%',
          backgroundColor: 'white',
          display: 'flex',
          flexDirection: 'column',
          justifyContent: 'center',
          alignItems: 'center',
        }}>
          <Typography variant='h5' gutterBottom>{image.name}</Typography>
          <canvas
            ref={canvasRef}
            width={canvasWidth}
            height={canvasHeight}
            style={{
              width: '95%',
              marginBottom: 15,
            }}
          />
          <CancelIcon onClick={() => setShotting(false)} fontSize='large' style={{ marginBottom: 100 }} />
        </div>
      </Slide>
    </div>
  )
}

export default Kaodashi;